.mobile-menu {
	--action-area-bg: var(--bg-color);
	--nav-bg: var(--white-dark);
	position: fixed;
	top: 0;
	left: 0;
	display: grid;
	grid-template-rows: 50px 1fr;
	max-width: 360px;
	width: 100vw;
	height: 100vh;
	z-index: 9;
	pointer-events: none;
	transform: translateX(-100%);
	transition: transform 0.3s ease-in-out;
	&--active {
		pointer-events: all;
		transform: translateX(0);
	}
	&__action-area {
		display: flex;
		align-items: center;
		gap: 10px;
		padding: 0 10px;
		background: var(--action-area-bg);
	}
	&__log-reg-area {
		flex: 1;
	}
	&__nav {
		display: block;
		padding: 20px 10px 60px;
		background: var(--nav-bg);
		overflow-y: auto;
		@media screen and (min-width: 360px) {
			padding: 20px 20px 60px;
		}
	}
	&__row {
		display: flex;
		gap: 10px;
		margin-bottom: 10px;
		@media screen and (min-width: 360px) {
			gap: 20px;
		}
	}
	&__list {
		flex: 1;
	}
	&__item {
		position: relative;
		margin-bottom: 10px;
		&--sub-menu {
			&::after {
				content: "";
				display: block;
				position: absolute;
				top: 20px;
				right: 16px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 4px 4px 0 4px;
				border-color: var(--font-color) transparent transparent
					transparent;
				pointer-events: none;
				transition: transform 0.2s ease-in-out;
			}
		}
		&--active {
			&::after {
				transform: rotate(180deg);
			}
		}
	}
	.registration-link {
		display: none;
		visibility: hidden;
	}
	@media screen and (min-width: 360px) {
		.registration-link {
			display: inline-flex;
			visibility: visible;
		}
	}
}
.sub-menu {
	--sub-menu-bg: var(--white);
	--sub-menu-link-hover-bg: var(--decor-color);
	--sub-menu-link-color: var(--font-color);
	display: none;
	visibility: hidden;
	margin-top: -10px;
	padding: 20px 10px 10px;
	background: var(--sub-menu-bg);
	border-radius: 0 0 10px 10px;
	&--active {
		display: block;
		visibility: visible;
	}
	&__item {
		margin-bottom: 3px;
	}
	&__link {
		position: relative;
		display: flex;
		align-items: center;
		padding-left: 40px;
		padding-right: 10px;
		width: 100%;
		height: 40px;
		color: var(--sub-menu-link-color);
		border-radius: 10px;
		cursor: pointer;
		&--current,
		&:hover {
			background: var(--sub-menu-link-hover-bg);
		}
	}
	&__link-info {
		display: inline-block;
		&::first-letter {
			text-transform: capitalize;
		}
	}
}
.mobile-lang {
	--mobile-lang-bg: var(--white);
	--lang-font-color: var(--font-color);
	--lang-link-current-bg: var(--decor-color);
	position: relative;
	display: flex;
	flex-direction: column;
	height: 40px;
	background: var(--mobile-lang-bg);
	border-radius: 10px;
	overflow: hidden;
	&::after {
		content: "";
		display: block;
		position: absolute;
		top: 20px;
		right: 16px;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 4px 4px 0 4px;
		border-color: var(--font-color) transparent transparent transparent;
		pointer-events: none;
		transition: transform 0.2s ease-in-out;
	}
	&--active {
		height: auto;
		&::after {
			transform: rotate(180deg);
		}
		.mobile-lang__link {
			&--current {
				background: var(--lang-link-current-bg);
			}
		}
	}
	&__item {
		order: 2;
		margin-bottom: 3px;
		&--current {
			order: 1;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		display: flex;
		align-items: center;
		padding: 0 10px;
		width: 100%;
		height: 40px;
		font-size: 12px;
		font-weight: 700;
		color: var(--lang-font-color);
		text-transform: capitalize;
		border-radius: 10px;
		cursor: pointer;
		&:hover {
			background: var(--lang-link-current-bg);
		}
	}
	&__img {
		display: block;
		width: 24px;
		height: 18px;
	}
	&__info {
		padding: 0 7px;
	}
}
