.popup {
	--popup-bg: var(--white);
	--popup-border: var(--white-dark);
	position: fixed;
	top: 20px;
	left: 50%;
	display: inline-block;
	padding: 7px;
	width: auto;
	z-index: 9;
	transform: translate(-50%, -40px);
	opacity: 0;
	pointer-events: none;
	transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
	&--active {
		transform: translate(-50%, 0);
		opacity: 1;
		pointer-events: all;
	}
	&__body {
		position: relative;
		display: inline-flex;
		padding: 15px 0;
		width: auto;
		height: auto;
		background: var(--popup-bg);
		border: 10px solid var(--popup-border);
		border-radius: 20px;
		box-shadow: 0px 0px 0px 7px var(--popup-bg);
	}
	&__close {
		position: absolute;
		top: -23px;
		right: -10px;
		z-index: 2;
	}
	&__content {
		margin-right: -5px;
		padding-right: 15px;
		padding-left: 15px;
		// min-width: 276px;
		min-width: 290px;
		max-height: calc(100vh - 150px);
		height: auto;
		font-size: 14px;
		// line-height: 16px;
		overflow-y: auto;
		&::-webkit-scrollbar-track {
			background: var(--white-dark);
		}
		a {
			color: var(--decor-color-2);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	.heading {
		--heading-bg: var(--white-dark);
	}
	@media screen and (min-width: 334px) {
		&__close {
			right: -17px;
		}
	}
	@media screen and (min-width: 440px) {
		&__close {
			right: -23px;
		}
		&__content {
			min-width: 376px;
		}
	}
}
