.bonus-card {
	display: block;
	max-width: 356px;
	color: var(--font-color);
	text-align: center;
	&__img-box {
		display: flex;
		justify-content: center;
		align-items: flex-end;
		margin-top: -90px;
		width: 100%;
		height: auto;
	}
	&__img {
		display: block;
		max-width: 400px;
		width: calc(100% + 84px);
		// min-width: 320px;
		height: auto;
	}
	&__info-box {
		display: block;
		padding: 20px 0;
		font-size: 24px;
		line-height: 1.2;
	}
	&__info {
		a {
			color: var(--decor-color-2);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
		&--big {
			font-size: 2.5em;
			line-height: 1;
			font-weight: 700;
		}
	}
	&__btn-row {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: 10px;
		margin-bottom: 15px;
	}
	&__link {
		display: inline-block;
		font-size: 12px;
		font-weight: 700;
		color: var(--decor-color-2);
		text-decoration: underline;
		cursor: pointer;
		&::first-letter {
			text-transform: capitalize;
		}
		&::after {
			content: "";
			display: inline-block;
			margin-left: 0.5em;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 4px 0 4px 4px;
			border-color: transparent transparent transparent
				var(--decor-color-2);
			transition: transform 0.2s ease-in-out;
		}
		&:hover {
			text-decoration: none;
			&::after {
				transform: translateX(0.2em);
			}
		}
	}
}

.bonus-info {
	display: block;
	max-width: 796px;
	width: calc(100vw - 64px);
	// font-size: 12px;
	line-height: 1.667;
	&__content {
		display: block;
		margin: 0 auto;
		max-width: 640px;
		width: 100%;
	}
	&__img-box {
		display: flex;
		justify-content: center;
		align-items: flex-end;
		width: 100%;
	}
	&__img {
		display: block;
		max-width: 640px;
		width: calc(100% + 30px);
	}
	&__title-box {
		display: block;
		margin-bottom: 15px;
		padding: 10px 0;
		font-size: 24px;
		line-height: 1;
		text-align: center;
	}
	&__title {
		&--big {
			font-size: 2.5em;
			font-weight: 700;
		}
	}
	&__info {
		display: block;
		margin-bottom: 2em;
		a {
			color: var(--decor-color-2);
			font-weight: 700;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	&__table {
		margin-bottom: 25px;
	}
	&__rules {
		counter-reset: item;
		li {
			margin-bottom: 1em;
			&::before {
				counter-increment: item;
				content: counter(item) ". ";
				display: inline-block;
				margin-right: 5px;
				color: inherit;
				font-weight: 700;
			}
		}
		a {
			color: var(--decor-color-2);
			font-weight: 700;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	&__link-close {
		color: var(--decor-color-2);
		font-weight: 700;
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
	@media screen and (min-width: 1900px) {
		max-width: 1236px;
		font-size: 14px;
		&__content {
			max-width: 940px;
		}
		&__title-box {
			font-size: 30px;
		}
		&__title {
			&--big {
				font-size: 3.33333em;
			}
		}
	}
}
