.them-filter {
	--them-filter-bg: var(--blue-light);
	--link-color: var(--white-dark);
	position: relative;
	z-index: 2;
	display: block;
	width: 100%;
	background: var(--them-filter-bg);
	box-shadow: 0px -5px 0px 0px rgba(123, 31, 162, 0.3);
	&__row {
		display: flex;
		justify-content: center;
		width: 100%;
		box-shadow: inset 0px -5px 0px 0px rgba(49, 27, 146, 1);
	}
	&__list {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		width: auto;
		padding: 0 10px 10px;
		text-align: center;
		overflow-y: hidden;
		overflow-x: auto;
	}
	&__item {
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: flex-start;
		padding: 0 30px 0 10px;
		&:last-child {
			padding: 0 10px;
			&::after {
				content: none;
			}
		}
		&::after {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			width: 20px;
			height: 40px;
			background: url("../img/them-filter/ornam_sep.svg") center
				center/contain no-repeat;
		}
	}
	&__link {
		display: inline-flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		height: 61px;
		&:hover,
		&--active {
			.them-filter__link-info {
				padding: 8px 15px;
				border-radius: 8px;
				color: var(--font-color);
				background: var(--white);
				box-shadow: none;
			}
		}
		&--sub-menu {
			.them-filter__link-info {
				position: relative;
				padding-right: 25px;
				&::after {
					content: "";
					display: block;
					position: absolute;
					top: 50%;
					right: 10px;
					transform: translateY(-25%);
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 4px 4px 0 4px;
					border-color: var(--decor-color) transparent transparent
						transparent;
				}
			}
			&:hover,
			&--active {
				.them-filter__link-info {
					padding-right: 25px;
				}
			}
		}
	}
	&__link-img-box {
		display: block;
		width: 40px;
		height: 40px;
	}
	&__link-img {
		display: block;
		width: 40px;
		height: 40px;
		border-radius: 5px;
		object-fit: cover;
	}
	&__link-info {
		display: inline-block;
		padding: 3px 15px;
		font-size: 12px;
		font-weight: 700;
		color: var(--link-color);
		white-space: nowrap;
		border-radius: 4px;
		background: #522ea9;
		box-shadow: 1px 1px 0 0 var(--blue),
			inset 1px 1px 0 0 var(--purple-light);
		&::first-letter {
			text-transform: capitalize;
		}
	}
	&__sub-menu {
		--sub-menu-bg: var(--black-dark);
		display: none;
		visibility: hidden;
		background: var(--sub-menu-bg);
		&--active {
			display: block;
			visibility: visible;
		}
	}
	&__catalog {
		display: none;
		visibility: hidden;
		&--active {
			display: block;
			visibility: visible;
		}
	}
	&__game-section {
		color: var(--white);
		background: var(--bg-color);
	}
}

.them-catalog {
	display: block;
	padding: 0 0 10px;
	width: 100%;
	&__row {
		display: flex;
		justify-content: center;
		width: 100%;
	}
	&__list {
		display: grid;
		grid-template-columns: repeat(auto-fill, 130px);
		justify-content: center;
		&--scroll {
			justify-content: start;
			grid-template-columns: repeat(10, 130px);
			grid-template-rows: 120px 120px;
			grid-auto-flow: column;
			overflow-x: auto;
			-webkit-mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 3%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
			mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 3%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
		}
	}
	&__item {
		display: block;
	}
	&__link {
		position: relative;
		display: inline-flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 15px 0 0;
		width: 130px;
		&:hover,
		&--active {
			.them-catalog {
				&__link-img {
					box-shadow: 0px 0px 0px 4px var(--white);
				}
				&__link-info {
					color: var(--font-color);
					background: var(--white);
					transform: translateY(-10px);
				}
			}
		}
	}
	&__link-img {
		display: block;
		width: 70px;
		height: 70px;
		border-radius: 9px;
		object-fit: cover;
	}
	&__link-info {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 10px;
		height: 35px;
		font-size: 12px;
		font-weight: 700;
		color: var(--white-dark);
		border-radius: 9px;
		transition: transform 0.2s ease-in-out;
		span {
			&::first-letter {
				text-transform: capitalize;
			}
		}
	}
}

.provider-catalog {
	--link-img-bg: var(--black);
	display: block;
	&__grid {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
		justify-content: center;
		gap: 0 20px;
		padding: 10px 0;
	}
	&__slider {
		--link-img-bg: var(--gray-dark);
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}

		.slick-list {
			padding: 0 40px;
			mask-image: linear-gradient(
				90deg,
				hsla(0, 0%, 100%, 0),
				#fff 3%,
				#fff 97%,
				hsla(0, 0%, 100%, 0)
			);
			@media screen and (min-width: 1300px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 3%,
					#fff 97%,
					hsla(0, 0%, 100%, 0)
				);
			}
			@media screen and (min-width: 1600px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 5%,
					#fff 95%,
					hsla(0, 0%, 100%, 0)
				);
			}
		}
		.provider-catalog {
			&__item {
				padding: 0 5px;
			}
			&__link {
				height: 50px;
				&:hover,
				&--active {
					.provider-catalog {
						&__link-img-box {
							--link-img-bg: var(--white);
						}
					}
				}
			}
			&__link-info {
				display: none;
				visibility: hidden;
			}
		}
	}
	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__link {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		cursor: pointer;
		&:hover,
		&--active {
			.provider-catalog {
				&__link-img-box {
					--link-img-bg: var(--white-dark);
				}
				&__link-info {
					color: var(--font-color);
					background: var(--white);
					transform: translateY(-10px);
				}
			}
		}
	}
	&__link-img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		max-width: 200px;
		width: 100%;
		min-width: 160px;
		height: 70px;
		background: var(--link-img-bg);
		border-radius: 10px;
		&--small {
			height: 50px;
		}
	}
	&__link-img {
		display: block;
		max-width: 160px;
		width: 80%;
		max-height: 50px;
		height: auto;
	}
	&__link-info {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		margin-top: -10px;
		padding: 0 10px;
		width: auto;
		min-width: 100px;
		height: 35px;
		font-size: 12px;
		font-weight: 700;
		color: var(--white-dark);
		border-radius: 9px;
		transition: transform 0.2s ease-in-out;
	}
}
