.action-area {
	position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 0;
    background: linear-gradient(0deg,var(--black-dark) 25%,rgba(238,130,238,0) 100%);
    z-index: 4;
	& > * + * {
		margin-left: 10px;
	}
	@media screen and (min-width: 600px) {
		display: none;
		visibility: hidden;
	}
}