.betting-category {
	display: flex;
	justify-content: center;
	margin-bottom: 10px;
	width: 100%;
	overflow-x: auto;
    &__list {
		display: flex;
		align-items: center;
		padding: 5px 0;
		width: auto;
		overflow-x: auto;
    }
    &__item {
		display: inline-block;
		padding: 0 5px;
		&:first-child {
			padding-left: 0;
		}
    }
    &__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 8px;
		width: auto;
		min-width: 40px;
		height: 40px;
		font-size: 12px;
		font-weight: 700;
		color: var(--font-color);
		border-radius: 4px;
		background: var(--white); 
		box-shadow: -4px 1px 10px 0px rgba(33, 33, 33, 0.1);
		overflow: hidden;
		&:hover,
		&--active {
			background: var(--decor-color);
			box-shadow: inset 1px 1px 0 0 var(--yellow-light),
			inset -1px -1px 0 0 var(--yellow-dark);
		}
		&--active {
			padding: 0 15px;
			.betting-category {
				&__link-info-box {
					width: auto;
				}
				&__link-info {
					transform: translateY(0);
				}
			}
		}
    }
    &__img {
		display: block;
		width: 24px;
		height: 24px;
    }
    &__link-info-box {
		display: block;
		width: 0;
    }
    &__link-info {
		display: inline-block;
		padding: 0 10px;
		white-space: nowrap;
		transform: translateY(200%);
		transition: transform 0.2s ease-in-out;
		&::first-letter {
			text-transform: capitalize;
		}
    }
}
