.small-btn {
	--btn-font-color: var(--font-color);
	--btn-bg-color: var(--decor-color);
	--btn-shadow-top: var(--yellow-light);
	--btn-shadow-bottom: var(--yellow-dark);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding: 0 15px;
	width: auto;
	height: 30px;
	font-size: 12px;
	font-weight: 700;
	color: var(--btn-font-color);
	background: var(--btn-bg-color);
	border-radius: 3px;
	box-shadow: inset 1px 1px 0px 0px var(--btn-shadow-top),
	inset -1px -1px 0px 0px var(--btn-shadow-bottom);
	transition: box-shadow 0.4s ease-in-out;
	cursor: pointer;
	&__info,
	span {
		display: inline-block;
		&::first-letter {
			text-transform: capitalize;
		}
	}
	&:hover {
		box-shadow: 1px 1px 0px 0px var(--btn-shadow-top),
			-1px -1px 0px 0px var(--btn-shadow-bottom);
	}
	&--blue {
		--btn-font-color: var(--white-dark);
		--btn-bg-color: var(--blue-light);
		--btn-shadow-top: var(--purple-light);
		--btn-shadow-bottom: var(--blue);
	}
	&--black {
		--btn-font-color: var(--white-dark);
		--btn-bg-color: var(--bg-color);
		--btn-shadow-top: var(--gray);
		--btn-shadow-bottom: #111;
	}
	&--white {
		--btn-font-color: var(--font-color);
		--btn-bg-color: var(--white-dark);
		--btn-shadow-top: var(--white);
		--btn-shadow-bottom: var(--gray-light);
	}
	&--small {
		padding: 0;
		max-width: 30px;
		width: 30px;
		min-width: 30px;
	}
	&__img {
		display: block;
		width: 18px;
		height: 18px;
	}
	&--active {
		.small-btn-st0 {
			fill: var(--decor-color);
		}
	}
}

.btn {
	--btn-font-color: var(--font-color);
	--btn-bg-color: var(--decor-color);
	--btn-shadow-top: var(--yellow-light);
	--btn-shadow-bottom: var(--yellow-dark);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding: 0 15px;
	width: auto;
	min-width: 200px;
	height: 60px;
	font-size: 14px;
	font-weight: 700;
	color: var(--btn-font-color);
	// text-transform: capitalize;
	background: var(--btn-bg-color);
	border-radius: 13px;
	box-shadow: inset 4px 4px 0px 0px var(--btn-shadow-top),
		inset -4px -4px 0px 0px var(--btn-shadow-bottom);
	transition: box-shadow 0.4s ease-in-out;
	cursor: pointer;
	span::first-letter {
		text-transform: capitalize;
	}
	&:hover {
		box-shadow: 4px 4px 0px 0px var(--btn-shadow-top),
			-4px -4px 0px 0px var(--btn-shadow-bottom);
	}
	&--blue {
		--btn-font-color: var(--white-dark);
		--btn-bg-color: var(--blue-light);
		--btn-shadow-top: var(--purple-light);
		--btn-shadow-bottom: var(--blue);
	}
	&--black {
		--btn-font-color: var(--white-dark);
		--btn-bg-color: var(--bg-color);
		--btn-shadow-top: var(--gray);
		--btn-shadow-bottom: #111;
	}
	&--medium {
		min-width: 160px;
	}
	&--small {
		max-width: 90px;
		min-width: 90px;
	}
	&.btn-chat {
		display: none;
		visibility: hidden;
	}
	&__triangle {
		display: block;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 10px 0 10px 15px;
		border-color: transparent transparent transparent var(--font-color);
		transform: translateX(25%);
	}
	&__img {
		display: block;
		margin-right: 10px;
		width: 30px;
		height: 30px;
	}
	input[type="submit"] {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		z-index: 2;
	}
	@media screen and (min-width: 600px) {
		&.btn-chat {
			display: inline-flex;
			visibility: visible;
			position: fixed;
			bottom: 20px;
			right: 30px;
			min-width: 140px;
			height: 50px;
			box-shadow: inset 2px 2px 0px 0px var(--btn-shadow-top),
				inset -2px -2px 0px 0px var(--btn-shadow-bottom),
				0px 2px 5px 0px rgba(24, 24, 24, 1);
			z-index: 8;
			&:hover {
				box-shadow: 2px 2px 0px 0px var(--btn-shadow-top),
					-2px -2px 0px 0px var(--btn-shadow-bottom);
			}
		}
	}
	@media screen and (min-width: 1921px) {
		&.btn-chat {
			right: calc(50vw - 930px);
		}
	}
}

.nav-link {
	--nav-link-color: var(--font-color);
	--nav-link-bg: var(--white);
	--nav-link-bg-hover: var(--decor-color);
	position: relative;
	display: flex;
	align-items: center;
	padding: 0 10px 0 0;
	width: auto;
	min-width: 100px;
	font-size: 12px;
	font-weight: 700;
	color: var(--nav-link-color);
	background: var(--nav-link-bg);
	border-radius: 10px;

	&--dark {
		--nav-link-color: var(--white);
		--nav-link-bg: var(--decor-color-2);
	}

	&:hover,
	&--current {
		--nav-link-color: var(--font-color);
		--nav-link-bg: var(--nav-link-bg-hover);
	}
	&__img {
		display: block;
		width: 40px;
		height: 40px;
	}
	&__info {
		display: inline-block;
		white-space: nowrap;
		&::first-letter {
			text-transform: capitalize;
		}
	}
	&__alert {
		position: absolute;
		top: -5px;
		right: -5px;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 16px;
		height: 16px;
		font-size: 10px;
		font-weight: 700;
		color: var(--white);
		border-radius: 50%;
		background: var(--red);
		z-index: 2;
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
}

.btn-close {
	--btn-font-color: var(--white-dark);
	--btn-bg-color: var(--blue-light);
	--btn-shadow-top: var(--purple-light);
	--btn-shadow-bottom: var(--blue);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	width: 30px;
	height: 30px;
	background: var(--btn-bg-color);
	border-radius: 3px 15px 3px 3px;
	box-shadow: inset 1px 1px 0px 0px var(--btn-shadow-top),
		inset -1px -1px 0px 0px var(--btn-shadow-bottom);
	transition: box-shadow 0.4s ease-in-out;
	cursor: pointer;
	&:hover {
		box-shadow: 1px 1px 0px 0px var(--btn-shadow-top),
			-1px -1px 0px 0px var(--btn-shadow-bottom);
	}
	&__img {
		display: block;
		width: 30px;
		height: 30px;
	}
}

.btn-row {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	gap: 20px;
	padding: 20px 0;
}
