.main-slider {
	--info-bg: var(--white);
	--info-border: var(--white-dark);
	&__slider {
		perspective: 1000px;
		background: var(--decor-color-2);
		.main-slider {
			&__img-link {
				transform: rotateY(90deg);
				transition: transform 0.6s ease-in-out;
			}
			&__info-box {
				transform: translateY(100%);
				transition: transform 0.6s ease-in-out;
			}
		}
		.slick-slide {
			&.slick-current {
				.main-slider {
					&__img-link {
						transform: rotateY(0);
						transition-delay: 0.3s;
					}
					&__info-box {
						transform: translateY(0);
						transition-delay: 0.3s;
					}
				}
			}
		}
	}
	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding-bottom: 40px;
	}
	&__item-bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		z-index: 1;
	}
	&__img-side {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		max-width: 600px;
		width: 100%;
		z-index: 2;
	}
	&__img-link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
	}
	&__img {
		display: block;
		max-width: 600px;
		width: 100%;
		max-height: 300px;
		height: auto;
	}
	&__info-side {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: -25px;
		padding: 0 17px;
		max-width: 600px;
		width: 100%;
		z-index: 3;
	}
	&__info-box {
		flex: 1;
		position: relative;
		display: block;
		padding: 35px 5px;
		max-width: 600px;
		width: 100%;
		font-size: 24px;
		color: var(--font-color);
		text-align: center;
		background: var(--info-bg);
		border-radius: 35px;
		border: 10px solid var(--info-border);
		box-shadow: 0px 0px 0px 7px var(--info-bg);
	}
	&__info {
		&--big {
			font-size: 3.333em;
			line-height: 1;
			font-weight: 700;
		}
	}
	&__btn-box {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
		margin-top: 15px;
		margin-bottom: -70px;
	}
	@media screen and (min-width: 760px) {
		&__item {
			flex-direction: row-reverse;
			gap: 10px;
		}
		&__img-side {
			flex: 1;
		}
		&__info-side {
			flex: 1;
			margin-top: 0;
			padding-top: 30px;
		}
	}
	@media screen and (min-width: 990px) {
		&__item {
			padding-left: 25px;
			padding-right: 25px;
			gap: 20px;
		}
	}
	@media screen and (min-width: 1200px) {
		&__item {
			padding-left: 40px;
			padding-right: 40px;
		}
		&__info-box {
			padding: 35px 20px;
		}
	}
}
