.account {
	&__grid {
		display: grid;
		justify-items: center;
		gap: 34px;
	}
	&__row {
		display: flex;
		justify-content: space-around;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
	}
	&__bonus-shape,
	&__shape {
		display: block;
		width: calc(100vw - 90px);
		max-width: 356px;
		color: var(--font-color);
		&--large {
			max-width: 1236px;
		}
		.heading {
			--heading-bg: var(--white-dark);
		}
	}		
	&__messaage {
		font-size: 12px;
		line-height: 1.6667;
		a {
			font-weight: 700;
			color: var(--decor-color-2);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	&__messaage-date {
		font-weight: 700;
	}
	&__img-box {
		position: relative;
		display: flex;
		justify-content: center;
		&--active {
			.account__img-gallery {
				transform: translate(-50%, 0);
				opacity: 1;
				pointer-events: all;
			}
		}
	}
	&__img-current,
	&__img-item {
		--shadow-color: var(--white-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		overflow: hidden;
		box-shadow: 0 0 0 5px var(--shadow-color);
		cursor: pointer;
		&--current,
		&:hover {
			--shadow-color: var(--decor-color);
		}
	}
	&__img-current {
		width: 90px;
		min-width: 90px;
		height: 90px;
	}
	&__img-item {
		width: 80px;
		min-width: 80px;
		height: 80px;
	}
	&__img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&__img-gallery {
		position: absolute;
		top: -5px;
		left: 50%;
		transform: translate(-50%, 20%);
		box-shadow: 0 4px 10px 0 rgb(21 21 21 / 10%);
		border-radius: 25px;
		z-index: 2;
		pointer-events: none;
		opacity: 0;
		transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
	}
	&__img-list {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: 18px;
		margin: 0 -20px;
		padding: 5px 15px;
		max-height: 300px;
		overflow-x: hidden;
		overflow-y: auto;
	}
	&__img-gallery-close {
		position: absolute;
		top: -20px;
		right: -20px;
	}
	&__favorite-game {
		position: relative;
		display: block;
		max-width: 300px;
		width: 100%;
	}
	&__favorite-remove {
		position: absolute;
		top: -5px;
		right: -4px;
		z-index: 5;
	}
	&__filter-box {
		.input-area {
			margin-top: 0;
			max-width: 180px;
			&::after {
				content: none;
			}
		}
	}
	&__filter-row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
		padding-bottom: 10px;
		width: 100%;
	}
	&__filter-item {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		width: 100%;
	}
	@media screen and (min-width: 760px) {
		&__filter-item {
			width: auto;
			.select--big {
				min-width: 180px;
			}
		}
	}
	@media screen and (min-width: 1100px) {
		&__filter-row {
			justify-content: flex-start;
		}
		&__filter-item {
			&:last-child {
				margin-left: auto;
			}
		}
	}
	@media screen and (min-width: 1600px) {
		&__shape {
			max-width: 556px;
			&--big {
				max-width: 1236px;
			}
			&--large {
				max-width: 1236px;
			}
		}
		&__form {
			padding: 0 90px;
		}
		&__form-row {
			display: flex;
			justify-content: center;
			align-items: flex-start;
			gap: 40px;
		}
		&__form-item {
			flex: 1;
			max-width: 380px;
		}
		&__messaage {
			font-size: 14px;
		}
	}
}
