*,
*:before,
*:after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Montserrat", sans-serif;
}
/* width / height */
::-webkit-scrollbar {
	width: 5px;
	height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
	background: var(--gray-light);
	width: 5px;
	height: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: var(--decor-color);
	width: 5px;
	height: 5px;
}
body {
	position: relative;
	padding-top: var(--header-height);
	font-size: 12px;
	font-weight: 400;
	color: var(--font-color);
	background: var(--bg-color);
	overflow-x: hidden;
	&.on-line {
		.registration {
			display: none;
			visibility: hidden;
		}
		.login {
			display: block;
			visibility: visible;
		}
		.action-area {
			display: none;
			visibility: hidden;
		}
	}
	.registration {
		display: block;
		visibility: visible;
	}
	.login {
		display: none;
		visibility: hidden;
	}
	@media screen and (min-width: 1600px) {
		font-size: 14px;
	}
}

.dn {
	display: none !important;
	visibility: hidden !important;
}

.decor-color {
	color: var(--decor-color);
}
.decor-bg {
	background: var(--decor-color);
}

h1,
.h1 {
	font-size: 24px;
}
h2,
.h2 {
	font-size: 14px;
	font-weight: 700;
	color: inherit;
	text-transform: uppercase;
	@media screen and (min-width: 360px) {
		font-size: 16px;
	}
}
h3,
.h3 {
	font-size: 14px;
}
h4,
.h4 {
	font-size: 12px;
}

b,
strong,
.bold {
	font-weight: 700;
}

.overlay {
	--overlay-bg: var(--gray-dark);
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: var(--overlay-bg);
	opacity: 0;
	z-index: 9;
	transform: translateY(-40%);
	transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
	pointer-events: none;
	&--active {
		pointer-events: all;
		opacity: 0.8;
		transform: translateY(0);
	}
}

.wrapper {
	display: block;
	margin: 0 auto;
	padding: 0 10px;
	max-width: 1920px;
	width: 100%;
}

main {
	min-height: 100vh;
}

// .slick-slider {
// }
.slick-prev,
.slick-next {
	--arrow-color: var(--white);
	--triangle-color: var(--black);
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 30px;
	background: var(--arrow-color);
	border-radius: 5px;
	box-shadow: 0px 4px 10px 0px rgba(21, 21, 21, 0.1);
	z-index: 3;
	&::before {
		content: "";
		width: 0;
		height: 0;
		border-style: solid;
		opacity: 1;
	}
	&:focus,
	&:hover {
		background: var(--arrow-color);
	}
}
.slick-prev {
	left: 10px;
	&::before {
		border-width: 6px 6px 6px 0;
		border-color: transparent var(--triangle-color) transparent transparent;
		transform: translateX(50%);
	}
}
.slick-next {
	right: 10px;
	&::before {
		border-width: 6px 0 6px 6px;
		border-color: transparent transparent transparent var(--triangle-color);
		transform: translateX(50%);
	}
}
@media screen and (min-width: 1240px) {
	.slick-prev {
		left: calc(50vw - 620px);
	}
	.slick-next {
		right: calc(50vw - 620px);
	}
}

.section {
	--section-color: var(--white);
	position: relative;
	display: block;
	padding: 15px 0;
	color: var(--section-color);
	&--black {
		background: var(--black-dark);
	}
	&.section--white {
		--section-color: var(--font-color);
		background: var(--white-dark);
		.heading {
			--heading-color: var(--section-color);
			--heading-bg: var(--white);
		}
		.slick-prev,
		.slick-next {
			--arrow-color: var(--white);
			--triangle-color: var(--black);
		}
	}
	&--testimonials {
		&::after {
			content: "";
			display: block;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 79px;
			background: var(--black);
			z-index: 1;
		}
		.wrapper {
			position: relative;
			z-index: 2;
		}
	}
	&--news {
		&::after {
			content: "";
			display: block;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 159px;
			background: var(--black);
			z-index: 1;
		}
		.wrapper {
			position: relative;
			z-index: 2;
		}
	}
	&--betting {
		&::after {
			content: "";
			display: block;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 65px;
			background: var(--black);
			z-index: 1;
		}
		.wrapper {
			position: relative;
			z-index: 2;
		}
	}
	.slick-prev,
	.slick-next {
		--arrow-color: var(--gray-dark);
		--triangle-color: var(--white);
		top: -45px;
		transform: translate(0, 0);
	}
	.slick-prev {
		left: 0;
	}
	.slick-next {
		right: unset;
		left: 45px;
	}
	@media screen and (min-width: 390px) {
		.slick-prev {
			left: 10px;
		}
		.slick-next {
			right: unset;
			left: 60px;
		}
	}
}
.heading {
	--heading-color: var(--white);
	--heading-bg: var(--gray-dark);
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 10px;
	margin-bottom: 20px;
	&--slider {
		padding-left: 90px;
	}
	&__line {
		flex: 1;
		display: block;
		width: 100%;
		height: 10px;
		background: var(--heading-bg);
		border-radius: 5px;
	}
	&__content {
		& * {
			margin-bottom: 0;
		}
	}
	&__link {
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding-right: 17px;
		font-size: 16px;
		font-weight: 700;
		color: var(--heading-color);
		text-transform: uppercase;
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			display: block;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 6px 0 6px 6px;
			border-color: transparent transparent transparent
				var(--heading-color);
		}
		&:hover {
			--heading-color: var(--decor-color);
		}
	}
	@media screen and (min-width: 390px) {
		&--slider {
			padding-left: 110px;
		}
	}
}
