.shape {
	--shape-bg: var(--white);
	--shape-border: var(--white-dark);
	display: inline-block;
	padding: 7px;
	width: auto;
	&--img {
		padding-top: 65px;
	}
	&--green {
		--shape-border: #41ae58;
	}
	&--yellow {
		--shape-border: var(--yellow);
	}
	&__close {
		position: absolute;
		top: -22px;
		right: -22px;
		z-index: 2;
	}
	&__body {
		position: relative;
		display: inline-flex;
		padding: 15px;
		width: auto;
		height: auto;
		background: var(--shape-bg);
		border: 10px solid var(--shape-border);
		border-radius: 20px;
		box-shadow: 0px 0px 0px 7px var(--shape-bg);
	}
}

.tab {
	display: block;
	width: 100%;
	&__menu {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 5px;
		margin-bottom: 15px;
		padding: 0 10px;
		&--center {
			justify-content: center;
			flex-wrap: wrap;
			// gap: 10px 20px;
			.tab {
				&__menu-item {
					flex-grow: 1;
				}
				&__menu-btn {
					width: 100%;
				}
			}
		}
	}
	&__menu-item {
		&--active {
			.tab {
				&__step {
					color: var(--white);
					background: var(--decor-color-2);
					.tab__icon {
						fill: var(--white);
					}
				}
				&__menu-btn {
					--btn-font-color: var(--font-color);
					--btn-bg-color: var(--decor-color);
					--btn-shadow-top: var(--yellow-light);
					--btn-shadow-bottom: var(--yellow-dark);
				}
			}
		}
	}
	&__menu-btn {
		--btn-font-color: var(--white-dark);
		--btn-bg-color: var(--blue-light);
		--btn-shadow-top: var(--purple-light);
		--btn-shadow-bottom: var(--blue);
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 5px;
		width: auto;
		height: 30px;
		font-size: 10px;
		font-weight: 700;
		color: var(--btn-font-color);
		background: var(--btn-bg-color);
		border-radius: 3px;
		box-shadow: inset 1px 1px 0 0 var(--btn-shadow-top),
			inset -1px -1px 0 0 var(--btn-shadow-bottom);
		transition: box-shadow 0.4s ease-in-out;
		cursor: pointer;
		&:hover {
			box-shadow: 1px 1px 0px 0px var(--btn-shadow-top),
				-1px -1px 0px 0px var(--btn-shadow-bottom);
		}
	}
	&__step {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 40px;
		min-width: 40px;
		height: 40px;
		font-size: 16px;
		font-weight: 700;
		color: var(--font-color);
		border-radius: 50%;
		background: var(--white-dark);
		cursor: pointer;
	}
	&__img {
		display: block;
		width: 20px;
		height: 20px;
	}
	&__icon {
		fill: var(--font-color);
	}
	&__menu-decor {
		flex: 1;
		display: block;
		height: 10px;
		border-bottom: 10px dotted var(--white-dark);
		pointer-events: none;
	}
	@media screen and (min-width: 360px) {
		&__menu-btn {
			font-size: 12px;
		}
	}
	@media screen and (min-width: 760px) {
		&__menu {
			&--center {
				gap: 10px 20px;
				.tab {
					&__menu-item {
						flex-grow: unset;
					}
					&__menu-btn {
						width: auto;
					}
				}
			}
		}
		&__menu-btn {
			padding: 0 15px;
			min-width: 160px;
			height: 60px;
			font-size: 14px;
			border-radius: 13px;
			box-shadow: inset 4px 4px 0 0 var(--btn-shadow-top),
				inset -4px -4px 0 0 var(--btn-shadow-bottom);
			&:hover {
				box-shadow: 4px 4px 0 0 var(--btn-shadow-top),
					-4px -4px 0 0 var(--btn-shadow-bottom);
			}
		}
	}
	@media screen and (min-width: 900px) {
		&__menu-btn {
			min-width: 200px;
		}
	}
}

.bonus-page {
	margin-bottom: 45px;
	&__item {
		padding: 30px 0 15px;
	}
}
.table-container {
	display: block;
	width: 100%;
	overflow-x: auto;
}
.table {
	--table-head-color: var(--white);
	--table-color: var(--font-color);
	--table-bg: var(--white-dark);
	--table-shadow: var(--white);
	border-collapse: collapse;
	width: 100%;
	thead {
		tr {
			td,
			th {
				color: var(--table-head-color);
				background: var(--decor-color-2);
			}
		}
	}
	tr {
		td,
		th {
			padding: 5px 10px 15px;
			white-space: nowrap;
			color: var(--table-color);
			background: var(--table-bg);
			box-shadow: inset 0px -10px 0px 0px var(--table-shadow);
			&:first-child {
				border-radius: 5px 0 0 5px;
			}
			&:last-child {
				border-radius: 0 5px 5px 0;
			}
			&.decor-bg {
				background: var(--decor-color);
			}
		}
	}
	a {
		color: var(--decor-color-2);
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
	&__sort {
		position: relative;
		display: inline-block;
		padding-right: 15px;
		cursor: pointer;
		text-transform: lowercase;
		&::first-letter {
			text-transform: uppercase;
		}
		&::before,
		&::after {
			content: "";
			display: block;
			position: absolute;
			right: 0;
			top: 50%;
			width: 0;
			height: 0;
			border-style: solid;
		}
		&::before {
			border-width: 0 4px 4px 4px;
			border-color: transparent transparent var(--white-dark) transparent;
			-webkit-transform: translateY(-125%);
			-ms-transform: translateY(-125%);
			transform: translateY(-125%);
		}
		&::after {
			border-width: 4px 4px 0 4px;
			border-color: var(--white-dark) transparent transparent transparent;
			-webkit-transform: translateY(25%);
			-ms-transform: translateY(25%);
			transform: translateY(25%);
		}
	}
	// &__ok {
	// 	color: #41ae58;
	// }
	&__cancel {
		opacity: 0.6;
		color: var(--gray-light);
	}
	&__reject {
		color: var(--red);
	}
	&__img-box {
		display: block;
		cursor: pointer;
	}
	&__img {
		display: block;
		width: 20px;
		height: 20px;
	}
}

.iframe-box {
	position: relative;
	display: block;
	min-width: 100%;
	min-height: 100vh;
	iframe {
		min-height: 100vh;
	}
}

ul.dot-list {
	padding-left: 10px;
	li {
		margin-bottom: 0;
		&::before {
			content: "";
			display: inline-block;
			margin-right: 5px;
			margin-bottom: 1px;
			width: 5px;
			height: 5px;
			background: var(--decor-color-2);
			border-radius: 50%;
		}
	}
}
