.tournament-card {
	display: block;
	max-width: 356px;
	color: var(--font-color);
	text-align: center;
	&__img-box {
		display: flex;
		justify-content: center;
		align-items: flex-end;
		margin-top: -90px;
		width: 100%;
		height: auto;
	}
	&__img {
		display: block;
		max-width: 400px;
		width: calc(100% + 84px);
		// min-width: 320px;
		height: auto;
	}
	&__info-box {
		display: block;
		padding: 12px 0;
		font-size: 24px;
		line-height: 1.2;
	}
	&__info {
		&--big {
			font-size: 2.5em;
			line-height: 1;
			font-weight: 700;
		}
	}
	&__btn-row {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: 10px;
		margin-bottom: 15px;
	}
	&__link {
		display: inline-block;
		font-size: 12px;
		font-weight: 700;
		color: var(--decor-color-2);
		text-decoration: underline;
		cursor: pointer;
		&::first-letter {
			text-transform: capitalize;
		}
		&::after {
			content: "";
			display: inline-block;
			margin-left: 0.5em;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 4px 0 4px 4px;
			border-color: transparent transparent transparent var(--decor-color-2);
			transition: transform 0.2s ease-in-out;
		}
		&:hover {
			text-decoration: none;
			&::after {
				transform: translateX(0.2em);
			}
		}
	}
}