.promo-slider {
	margin: 0 -10px;
	display: none;
	visibility: hidden;
	&.slick-initialized {
		display: block;
		visibility: visible;
	}
	.slick-prev {
		left: 0;
	}
	.slick-next {
		right: 0;
	}
	.slick-slide {
		padding: 0 10px;
		text-align: center;
	}
	@media screen and (min-width: 420px) {
		margin: 0;
		.slick-slide {
			padding: 0 5px;
		}
	}
	@media screen and (min-width: 450px) {
		.slick-list {
			// padding-right: calc(100% - 430px);
			mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 2%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
		}
	}
